import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';

import { RxUtilityModalsService } from '@helix/platform/ui-kit';
import {
  IComponentCanDeactivate,
  IDesignerConfiguration,
  RX_APPLICATION,
  RxBundleCacheService,
  RxComponentCanDeactivateGuard,
  RxDefinitionNameService,
  RxPageTitleService
} from '@helix/platform/shared/api';

import { ProcessDesignerComponent } from '../process-designer/process-designer.component';

@Component({
  selector: 'rx-process-designer-page',
  templateUrl: './process-designer-page.component.html'
})
export class ProcessDesignerPageComponent implements OnInit, OnDestroy, IComponentCanDeactivate {
  @ViewChild(ProcessDesignerComponent)
  private processDesignerComponent: ProcessDesignerComponent;

  configuration: IDesignerConfiguration;
  isInitialized = false;

  private isNewProcess: boolean;
  private subscription: Subscription;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private rxBundleCacheService: RxBundleCacheService,
    private rxComponentCanDeactivateGuard: RxComponentCanDeactivateGuard,
    private rxDefinitionNameService: RxDefinitionNameService,
    private rxPageTitleService: RxPageTitleService,
    private rxUtilityModalsService: RxUtilityModalsService,
    private translateService: TranslateService
  ) {}

  ngOnInit(): void {
    this.rxComponentCanDeactivateGuard.setPageComponent(this);

    this.subscription = this.activatedRoute.params.subscribe(({ definitionName, bundleId }) => {
      this.rxBundleCacheService.bundleId = bundleId || this.rxDefinitionNameService.getBundleId(definitionName);

      this.isInitialized = true;
      this.isNewProcess = !definitionName;

      this.rxPageTitleService.set([
        this.rxDefinitionNameService.getDisplayName(definitionName),
        this.translateService.instant('com.bmc.arsys.rx.client.process-designer.title')
      ]);

      this.configuration = {
        ...this.configuration,
        bundleId: this.rxBundleCacheService.bundleId,
        definitionName
      };
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.rxComponentCanDeactivateGuard.setPageComponent(null);
  }

  canDeactivate(): boolean {
    return this.processDesignerComponent?.canDeactivate() ?? true;
  }

  confirmDeactivation(): Promise<boolean> {
    return this.rxUtilityModalsService.confirmUnsavedChanges();
  }

  onCloseDesigner(): void {
    this.router.navigate([
      RX_APPLICATION.innovationStudioBundleId,
      this.rxBundleCacheService.bundleId,
      'process-definitions'
    ]);
  }

  onDefinitionErrorLoading(): void {
    this.router.navigate(['new2', this.rxBundleCacheService.bundleId], { relativeTo: this.activatedRoute.parent });
  }

  onDefinitionSaved(processDefinitionName: string): void {
    if (this.isNewProcess) {
      this.router.navigate(['edit2', processDefinitionName], { relativeTo: this.activatedRoute.parent });
    }
  }
}
